.board {
  margin: 20px auto;
  width:60%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.card {
  transform-style: preserve-3d;
  transition: 0.6s;
  position: relative;
  width: 120px;
  height: 120px;
  margin: 6px;
}

.card div {
  backface-visibility: hidden;
  border-radius: 10px;
  background: #f4f5f7; 
  position: absolute;
  width: 100%;
  height: 100%;
}

.front {
  font-size: 50px;
  line-height: 250%;
}

.back {
  transform: rotateY(180deg);
}

.back img {
  width: 100%;
  height: 100%;
}

.flipped {
  transform: rotateY(180deg);
}